.Servicios {
  &__Services {
    background-color: $color-white3;
  }
  &__Wonoma {
    background-image: url(../../assets/wonoma-bg.png);
    background-size: 50%;
    transition: 4s linear;
    will-change: background-position;
    background-position: 35% 89%;
    background-position: -57.5% 89%;
    background-position: -250% 89%;
    background-position: right 12.5%;

    display: flex;
    justify-content: space-between;
  }
}

.services-container {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

.services-subcontainer {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.services-box {
  width: 31%;
  height: 15vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  transition: 3s ease-out;
  will-change: background-size;
  background-color: $color-secondary;
  &__1 {
    background-image: url("../../assets/services3.JPG");
    background-position: center 35%;
  }
  &__2 {
    background-image: url("../../assets/services2.jpg");
    background-position: center 50%;
  }
  &__3 {
    background-image: url("../../assets/services3.png");
    background-position: center 30%;
  }
  &:hover {
    background-size: 120%;
    & .services-box-content {
      color: $color-white;
    }
  }
}

.services-box-content {
  width: 85%;
  height: 80%;
  margin: 0;
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  will-change: color;
  border: 2px solid $color-variation;
  color: $color-variation;
}

.wonoma-container {
  width: 100%;
  display: flex;
  &:hover .wonoma-image-subcontainer {
    background-size: 106%;
  }
}

.wonoma-image-container {
  width: 50%;
  height: 50vw;
  display: flex;
  flex-wrap: wrap;
  background-color: $color-white2;
}

.wonoma-image-subcontainer {
  box-shadow: inset 0 0 85px 0 rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 2;
  background-position: center;
  background-repeat: no-repeat;
  background-color: $color-variation-dark;
  background-image: url("../../assets/wonoma-img.png");
  height: 50vw;
  width: 100%;
  background-size: 100%;
  background-position: center 56%;
  transition: 5s linear;
  will-change: background-size;
}

.wonoma-content-container {
  width: 50%;
  padding-top: 5vw;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.wonoma-content {
  position: relative;
  z-index: 2;
  width: 85%;
  height: 35vw;
  display: flex;
  flex-direction: column;
}

.wonoma-title {
  text-align: center;
  font-weight: 500;
  font-size: 1.3vw;

  color: $color-white;
}

.wonoma-logo-container {
  width: 15vw;
  margin: 0 auto 1vw;
  & img {
    width: 100%;
  }
}

.wonoma-box-container {
  display: flex;
  margin: 3vw 0 4vw;
  justify-content: space-between;
}

.wonoma-box {
  width: 32%;
  padding: 1.5vw 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.65);
}

.icon-container {
  width: 4vw;
  height: 4vw;
  margin: 0;
  background-position: center;
  background-repeat: no-repeat;
  &__1 {
    background-image: url("../../assets/icon-clock-gray.svg");
    background-size: 95%;
  }
  &__2 {
    background-image: url("../../assets/icon-check-gray.svg");
    background-size: 70%;
  }
  &__3 {
    background-image: url("../../assets/icon-offer-gray.svg");
    background-size: 107%;
  }
}

.wonoma-box-title {
  margin: 1vw 0;
  font-family: "Merriweather", sans-serif;
  font-size: 1.2vw;
  text-align: center;
  text-transform: uppercase;
  width: 88%;
  color: $color-variation;
}

.wonoma-box-text {
  font-size: 0.85vw;
  width: 85%;
  text-align: center;

  color: $color-font-gray;
}

.wonoma-content a {
  margin: 0 auto;
  padding: 0.5vw 2.5vw;
  border-radius: 5px;
  font-size: 1.4vw;
  text-decoration: none;
  text-transform: uppercase;
  outline: none;
  font-weight: 500;
  font-size: 1.2vw;
  font-family: "Poppins", sans-serif;
  opacity: 1;
  transition: 0.3s;
  will-change: opacity;
  border: 2px solid $color-white;
  background: transparent;
  color: $color-white;

  &:hover {
    color: $color-variation-dark;
    background: $color-white;
    border: 2px solid $color-white;
  }
}
