.Prensa {
  background-image: url("../../assets/sucursales-bg-2.jpg");
  background-size: 100%;
  background-color: $color-secondary;
}

.prensa-bg-container {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 1vw;
}

.prensa-container-title {
  margin: 2vw 0 0;
  font-size: 1.6vw;

  text-align: center;
  color: $color-variation;
}

.prensa-container {
  width: 100%;
  margin: 3vw 0 4vw;
  position: relative;
  display: flex;
  align-items: center;
}

.prensa-subcontainer {
  width: 100%;
  overflow: hidden;
}

.arrow-icon-container {
  width: 4vw;
  height: 4vw;
  position: absolute;
  transition: 0.3s;
  will-change: opacity;
  &__left {
    left: -4.5vw;
  }
  &__right {
    right: -4.5vw;
    transform: rotateZ(180deg);
  }
  &__active {
    opacity: 0.8;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }
  &__inactive {
    opacity: 0.3;
    pointer-events: none;
  }
  & img {
    width: 100%;
  }
}

.prensa-box-wrapper {
  display: flex;
  transition: 1s;
  will-change: margin;
  &__page1 {
    margin-left: 0;
  }

  &__page2 {
    margin-left: -100%;
  }
  &__page3 {
    margin-left: -166.67%;
  }
  &__notas {
    width: 300%;
  }
  &__imagenes {
    width: 200%;
  }
}

.prensa-box-container {
  width: 33.33%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.prensa-box {
  width: 80%;
  border-radius: 6px;
  cursor: pointer;
  overflow: hidden;
  background-color: $color-secondary;
  &:hover .prensa-box-image-container img {
    transform: scale(1.05);
  }
  &:hover .prensa-box-title {
    color: $color-variation;
  }
}

.prensa-box-image-container {
  width: 100%;
  height: 12.5vw;
  overflow: hidden;
  & img {
    width: 100%;
    transition: 3s;
    will-change: transform;
  }
}

.prensa-box-title {
  width: 85%;
  padding: 1vw 0;
  margin: 0 auto;
  text-align: center;
  transition: 0.3s;
  will-change: color;
  color: $color-white;
}
